@import url(https://fonts.googleapis.com/css?family=Montserrat:400,700&display=swap);
body {
  font-family: "Montserrat", sans-serif;
  background: #F5F5F5;
  margin: 20px auto;
}

.fa-github {
  color: lightslategray;
  font-size: 20px;
}

.fa-github:hover {
  color: #138496;
}
.app {
  margin-top: 20px; }
  .app .row {
    margin: 20px 0; }
  .app .loading-container {
    margin-top: 30px;
    font-size: 20px;
    letter-spacing: 1px; }

#navbar ul .nav-item a {
  color: #303030; }
  #navbar ul .nav-item a :hover {
    color: #138496; }

#navbar ul .nav-item .title-p {
  padding: 0;
  margin: 0;
  letter-spacing: 3px; }

@media only screen and (max-width: 575px) {
  #navbar > ul > li:nth-child(odd) {
    padding: 20px 0;
    padding: 20px initial; } }

#cart h3 {
  margin-bottom: 40px; }

#cart ol {
  padding: 0; }
  #cart ol li {
    border-bottom: 1px solid #303030; }

#cart .delete-cart-item {
  padding-left: 20px;
  cursor: pointer; }

#cart .total-parapraph {
  border-top: double;
  padding: 10px;
  margin: 20px 0 16px 30px; }

#book ul {
  list-style-type: none;
  padding: 0; }
  #book ul .row-book {
    padding: 20px;
    border: 1px solid #138496;
    border-radius: 5px; }
    #book ul .row-book .col-sm {
      padding: 20px; }
      #book ul .row-book .col-sm:not(.col-img) {
        text-align: justify;
        position: relative; }
      #book ul .row-book .col-sm .buy-btn {
        letter-spacing: 2px; }
      #book ul .row-book .col-sm .more-info-link {
        position: absolute;
        bottom: 0;
        right: 30px;
        color: #303030; }
        #book ul .row-book .col-sm .more-info-link:hover {
          color: #138496; }
        #book ul .row-book .col-sm .more-info-link .fa-external-link-alt {
          font-size: 8px;
          vertical-align: super; }

